<template>
    <div id="allring" style="width: 100%; height: 100%;" ref="Ringchart"></div>
</template>
<script>
import * as echarts from 'echarts';

export default {
    data() {
    return {};
  },
  props: ["title","dbData"],
    mounted(){
        
        var myChart = echarts.init(this.$refs.Ringchart);
        var data1=this.dbData;
        var title=this.title;
    var app = {};
    console.log(data1);
    var option;

    option = {
      title : {
            show:true, //显示策略，默认值true,可选为：true（显示） | false（隐藏）
            text: title, //主标题文本，'\n'指定换行
            x:'right',
            textStyle: { //主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
                fontFamily: '苹方-简体',
                fontSize: 15,
                fontStyle: 'normal',
                fontWeight: 'bolder',
                color:'white',
            },
      },
  tooltip: {
    trigger: 'item'
  },
  legend: {
    // top: '0%',
    // left: 'center'
    orient: 'vertical',
     x:'right',      //可设定图例在左、右、居中
    y:'center',     //可设定图例在上、下、居中
    textStyle: {
      color:'rgb(255,255,255,0.9)'
    },
  },
  series: [
    {
      name: 'Access From',
      type: 'pie',
      radius: ['50%', '80%'],
      center: ['35%', '50%'],
      avoidLabelOverlap: false,
      itemStyle: {
        borderRadius: 10,
        // borderColor: '#fff',
        borderWidth: 2
      },
      label: {
        show: false,
        position: 'center'
        
      },
      emphasis: {
        label: {
          show: true,
          fontSize: 20,
          fontWeight: 'bold'
        }
      },
      labelLine: {
        show: false
      },
      data:data1
    }
  ]
};

    if (option && typeof option === 'object') {
      myChart.setOption(option);
    }

   // window.addEventListener('resize', myChart.resize);
    }
}
</script>