<template>
      <div id="SB" style="width: 100%; height: 100%;"></div>        
</template>
<script>
import * as echarts from 'echarts';
export default{
  mounted(){
    var chartDom = document.getElementById('SB');
var myChart = echarts.init(chartDom);
var option;
var years=["1998-2003","2003-2008","2008-2013","2013-2018","2018-2023"];
option = {
  tooltip: {
    trigger: 'axis',
    axisPointer: {
      // Use axis to trigger tooltip
      type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
    }
  },
  legend: {
    textStyle: {
      color:'rgb(255,255,255,0.9)',

    },
    orient: 'vertical',
    x:'5%',      //可设定图例在左、右、居中
    y:'40%',     //可设定图例在上、下、居中
  },
  grid: {
    top:'10%',
    left: '4%',
    right: '4%',
    bottom: '10%',
    containLabel: true
  },
  xAxis: {
