      color:'white', //坐标的字体颜色
      },
    },
   
  },
  yAxis: {
    type: 'category',
    data: years,
    position: 'right',
    axisLabel: {
      textStyle:{
      color:'white', //坐标的字体颜色
      },
    },
    
  },
  
  series: [
  
    {
      name: 'CNVD',
      type: 'bar',
      stack: 'total',
      label: {
        // show: true
      },
      emphasis: {
        focus: 'series'
      },
      data: [62, 106, 588, 1232, 1829],
     
             itemStyle:{
                color: {
                type: 'linear',
                x: 1,
