                x2: 0,
                y2: 1,
                colorStops: [
                    {offset: 0, color: '#3c4ffc'},
                    {offset: 1, color: '#b337fc'}
                ],
                globalCoord: true // 缺省为 false
            }
            }
      
     
    },
    {
      name: 'OSV ',
      type: 'bar',
      stack: 'total',
      label: {
        // show: true
      },
      emphasis: {
        focus: 'series'
      },
      data: [33, 47,492, 1107, 1655],
      itemStyle:{
                color: {
                type: 'linear',
                x: 1,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {offset: 0, color: '#7400bf'},
                    {offset: 1, color: '#ff7bac'}
                
                ],
                globalCoord: true // 缺省为 false
            }
            }
    },
    {
      name: 'NVD',
      type: 'bar',
      stack: 'total',
      label: {
        // show: true
