      itemStyle:{
                color: {
                type: 'linear',
                x: 1,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                {offset: 0, color: '#3c4ffc'},
                    {offset: 1, color: '#ffa7fc'}
                ],
                globalCoord: true // 缺省为 false
            }
            }
    },
  ]
};
option && myChart.setOption(option);
}
}
