pdate_251:*:*:*:*:*:*\", \"cpe:2.3:a:oracle:jre:1.7.0:update_76:*:*:*:*:*:*\", \"cpe:2.3:a:oracle:jre:1.7.0:update171:*:*:*:*:*:*\"]","affected_version_integrity":1,"cvss_score_v3":9.1,"cvss_score_v2":10.0,"severity":"Critical","type":"[\"502\"]","cwe_id":"[\"264\"]","description_cn":"Oracle Java 7在Update 11之前的多个漏洞允许远程攻击者通过以下方式执行任意代码：(1)使用JmxMBeanServer类中的公共getMBeanInstantiator方法获得对私有MBeanInstantiator对象的引用，然后使用findClass方法检索任意Class引用；(2)以递归的方式使用Reflection API，绕过java.lang.invoke.MethodHandles.Lookup.checkSecurityManager方法，由于sun.reflect.Reflection.getCallerClass方法无法跳过与新反射API相关的框架，2013年1月在野外被利用，正如Blackhole和Nuclear Pack所证明的，与CVE-2012-4681和CVE-2012-3174是不同的漏洞。注意：有些人将递归反射API问题映射到CVE-2012-3174，但CVE-2012-3174是针对一个不同的漏洞，截至20130114，其细节还没有公开。  CVE-2013-0422同时涵盖了JMX\/MBean和Reflection API问题。  注意：最初报道说Java 6也有漏洞，但记者收回了这一说法，称Java 6不可利用，因为相关代码的调用方式不会绕过安全检查。  注意：截至20130114，一个可靠的第三方声称findClass\/MBeanInstantiator向量在Oracle Java 7 Update 11中没有修复。  如果仍然存在易受攻击的情况，那么可能会为未修复的问题创建一个单独的CVE标识。","description_cn_integrity":1,"refs":"[\"http:\/\/www.kb.cert.org\/vuls\/id\/625617\", \"http:\/\/krebsonsecurity.com\/2013\/01\/zero-day-java-exploit-debuts-in-crimeware\/\", \"http:\/\/malware.dontneedcoffee.com\/2013\/01\/0-day-17u10-spotted-in-while-disable.html\", \"http:\/\/blog.fireeye.com\/research\/2013\/01\/happy-new-year-from-new-java-zero-day.html\", \"http:\/\/labs.alienvault.com\/labs\/index.php\/2013\/new-year-new-java-zeroday\/\", \"https:\/\/threatpost.com\/en_us\/blogs\/nasty-new-java-zero-day-found-exploit-kits-already-have-it-011013\", \"http:\/\/www.oracle.com\/technetwork\/topics\/security\/alert-cve-2013-0422-1896849.html\", \"http:\/\/immunityproducts.blogspot.ca\/2013\/01\/confirmed-java-only-fixed-one-of-two.html\", \"https:\/\/www-304.ibm.com\/connections\/blogs\/PSIRT\/entry\/oracle_java_7_security_manager_bypass_vulnerability_cve_2013_04224?lang=en_us\", \"https:\/\/partners.immunityinc.com\/idocs\/Java%20MBeanInstantiator.findClass%200day%20Analysis.pdf\", \"http:\/\/rhn.redh